/* Thiết lập chung */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.6;
  color: #333;
  background-color: #f8f9fa;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Header */
header {
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 1rem 0;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.brand {
  font-size: 1.5rem;
  font-weight: bold;
  color: #0d6efd;
  text-decoration: none;
}

.nav-links {
  display: flex;
  list-style: none;
}

.nav-links li {
  margin-left: 20px;
}

.nav-links a {
  color: #333;
  text-decoration: none;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #0d6efd;
}

/* Main content */
main {
  min-height: calc(100vh - 160px);
  padding: 2rem 0;
}

/* Posts */
.post-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.post-card {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.post-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.post-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.post-content {
  padding: 15px;
}

.post-title {
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: #333;
  text-decoration: none;
}

.post-meta {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 0.85rem;
  color: #666;
}

.post-excerpt {
  color: #666;
  margin-bottom: 15px;
}

/* Post Detail */
.post-detail {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.post-detail-header {
  margin-bottom: 20px;
}

.post-detail-title {
  font-size: 2rem;
  margin-bottom: 10px;
}

.post-detail-meta {
  display: flex;
  justify-content: space-between;
  color: #666;
  font-size: 0.9rem;
}

.post-detail-image {
  width: 100%;
  max-height: 500px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
}

.post-detail-content {
  line-height: 1.8;
}

/* Comments */
.comments-section {
  margin-top: 30px;
}

.comments-header {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.comment {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 15px;
}

.comment-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.comment-user {
  font-weight: bold;
}

.comment-date {
  color: #666;
  font-size: 0.85rem;
}

.comment-content {
  margin-bottom: 10px;
}

.comment-form {
  margin-top: 20px;
}

/* Forms */
form {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: 500;
}

input,
textarea,
select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

textarea {
  min-height: 150px;
}

button {
  background-color: #0d6efd;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #0b5ed7;
}

button.secondary {
  background-color: #6c757d;
}

button.secondary:hover {
  background-color: #5c636a;
}

button.danger {
  background-color: #dc3545;
}

button.danger:hover {
  background-color: #bb2d3b;
}

/* Rating */
.rating {
  display: flex;
  margin-bottom: 15px;
}

.star {
  color: #ccc;
  font-size: 1.5rem;
  cursor: pointer;
}

.star.filled {
  color: #ffc107;
}

/* Admin Dashboard */
.admin-dashboard {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.stat-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: bold;
  color: #0d6efd;
  margin-bottom: 10px;
}

.stat-label {
  color: #666;
}

/* Footer */
footer {
  background-color: #343a40;
  color: #fff;
  padding: 1.5rem 0;
  text-align: center;
}

.footer-links {
  display: flex;
  justify-content: center;
  list-style: none;
  margin-bottom: 15px;
}

.footer-links li {
  margin: 0 15px;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
}

.footer-text {
  font-size: 0.9rem;
  color: #adb5bd;
}

/* Responsive */
@media (max-width: 768px) {
  .post-list {
    grid-template-columns: 1fr;
  }

  .admin-dashboard {
    grid-template-columns: 1fr;
  }
}

/* Utilities */
.text-center {
  text-align: center;
}

.mt-3 {
  margin-top: 1.5rem;
}

.mb-3 {
  margin-bottom: 1.5rem;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left-color: #0d6efd;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
